body{
    margin: 0;
    padding: 0;
    background: linear-gradient(90deg, #9deceb, #ffd37c);
    height: 100vh;
    display: grid;
    align-items: center;
}
.container{
    margin: auto;
    box-sizing: border-box;
    width: 50%;
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 2rem;
    backdrop-filter: blur(0.5rem);
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    justify-items: center;
    align-items: center;
}
.container>h1:first-child{
    grid-column: 1/4;
    height: 3rem;
    text-align: center;
}
.container>div{
    height: 8rem;
    width: 100%;
    background-color: rgba(0,0,0,0.8);
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(255,255,255,0.9);
}
.versus{
    font-size: 40px;
}
h2:last-of-type{
    grid-column: 3/4;
}
.container>button{
    grid-column: 1/4;
    width: 20%;
    height: 2rem;
    border-radius: 1rem;
    background-color: none;
    transition: all 200ms ease;
}
.container>button:hover{
    background-color: rgba(0,0,0,0.8);
    color: white;
}
div:first-of-type>img{
    transform: rotate(180deg);
    transform: scaleX(-1);
}
img{
    height: 50%;
}
h2{
    text-align: center;
}